:root {
	--text-color-primary: #000;
	--text-color-invert: #fff;
	--text-color-brand: #92002F;
	--border-color-primary: #000;
	--border-color-neutral: #000;
	--border-color-brand: #87002b;
	--bg-color: #fff;
	--bg-color-brand: #92002F;
	--bg-color-hover: #f0f0f0;
	--bg-color-hover-light: #f0f0f0;
	--bg-color-hover-brand: #a20034;
	--border-radius: 8px;
}

.jf-hide {
	display: none;
}

.jf-p-left_xsmall {
	padding-left: 8px;
}

.jf-p-left_small {
	padding-left: 12px;
}

.jf-p-right_small {
	padding-right: 12px;
}

.jf-p-vertical_small {
	padding-top: 12px;
	padding-bottom: 12px;
}
.jf-m-vertical_small {
	margin-top: 12px;
	margin-bottom: 12px;
}
.jf-m-vertical_medium {
	margin-top: 16px;
	margin-bottom: 16px;
}
.jf-m-vertical_large {
	margin-top: 24px;
	margin-bottom: 24px;
}