.banner {
    display: block;
    position: relative;
    color: var(--text-color-primary);
    width: 100%;
    height: 100vh;
    background-image: url('../assets/images/building.jpg');
    background-size: contain;
    background-position: center bottom;
    background-repeat: no-repeat;
    
    display: flex;
}

.intro, .hero {
    position: relative;
    overflow: hidden;
    width: 100%;
    max-width: 100%;
    transition: all 1s linear;
}
.intro {
    max-width: 40%;
    display: flex;
}
.hero {
    width: 60%;
}
.intro-greet {
    width: 100%;

    -ms-flex-item-align: center;
    -ms-grid-row-align: center;
    align-self: center;
}
.intro .text-brand {
    color: var(--text-color-brand);
}
.intro .title {
    font-size: 60px;
    font-weight: 900;
    line-height: 68px;
}
.intro .role {
    font-size: 32px;
    font-weight: 100;
    margin: 8px 0;
}
.intro .bio {
    font-size: 18px;
}
.btn-about {
    max-width: 150px;
}

.hero-image {
    background-image: url('../assets/images/self.png');
    background-size: contain;
    background-position: right bottom;
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
}
.hero-image img {
    width: 80%;
}

@media only screen and (min-width: 78rem) {
    .spotlight {
        display: block;
    }
}

@media only screen and (min-width: 48em) {
    .banner {
        padding: 0 100px;
    }
}