.header-container {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;

    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;

    height: 80px;
    border-bottom: 1px solid #00000017;
    position: fixed;
    color: var(--text-color-primary);
    top: 0;
    left: 0;
    right: 0;
    min-height: 80px;
    padding: 0 1.25rem;
    transition: all 0.6s ease-in-out;
    z-index: 10;
}

.menus {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;

    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}

.logo,
.logo-image {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;

    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;

    height: 26px;
    padding-right: 8px;
    cursor: pointer;
    text-decoration: none;
    letter-spacing: 3px;
    font-size: 14px;
    color: var(--text-color-primary);
}

@media only screen and (min-width: 48em) {
    .header-container {
        padding: 0 100px;
    }
}

.desk-nav {
    margin-left: 20px;
    display: none;
}

.desk-nav li {
    display: inline-block;
    position: relative;
}

.desk-nav li label {
    font-weight: 600;
    font-size: 15px;
    padding: 12px 26px;
    cursor: pointer;
}

.desk-nav li label:hover {
    background-color: var(--bg-color-hover);
    border-radius: var(--border-radius);
}

.desk-nav li ul {
    position: absolute;
    padding: 12px 0;
    min-width: 172px;
    top: 50px;
    left: 0;
    background-color: #fff;
    border-radius: var(--border-radius);
    color: var(--text-color-primary);
    border: 1px solid var(--border-color-primary);
    min-width: 210px;
}

.desk-nav li ul li {
    padding: 8px 18px;
    min-width: 174px;
    cursor: pointer;
}

.desk-nav li ul li:hover {
    background-color: var(--bg-color-hover-light);
}

.desk-nav li ul li svg {
    width: 28px;
}

.nav-menus:not(.active) {
    display: none;
}

@media only screen and (min-width: 78rem) {
    .desk-nav {
        display: block;
    }
}

.header-actions {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;

    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;

    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
}